<template>
	<div v-loading="loading"
    element-loading-text="拼命登录中">
		<header class="login-header">
			<img src="../images/logo.png">
		</header><!-- /header -->
		<div class="login-content">
			<div class="content-wrap clearfix">
				<img class="pull-left" src="../images/loginbox.png">
				<div class="pull-left login-wrap">
					<p class="login-title f20 color-666">登录</p>
					<form>
						<div class="login-wrap-row">
							<p class="login-wrap-row-tl">
								<span class="iconfont icon-iconuser color-666 f20"></span>
							</p>
							<p>
								<el-input class="loing-ipt" v-model="acount" placeholder="请输入账号"></el-input>
							</p>
						</div>
						<div class="login-wrap-row">
							<p class="login-wrap-row-tl">
								<span class="iconfont icon-mima color-666 f20"></span>
							</p>
							<p>
								<el-input class="loing-ipt" v-model="password" type="password" placeholder="请输入密码"></el-input>
							</p>
						</div>
					</form>	
					<el-button type="primary" class="login-btn" @click="loggedIn">立即登录</el-button>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import { toLogin } from '../api/api';
	export default {
		data() {
			return {
				acount: 'tresor',
				password: '123456',
				loading: false
			}
		},
		methods: {
			loggedIn: function(){
				if(this.$utils.isEmpty(this.acount) || this.$utils.isEmpty(this.password)){
					this.$message('账号或密码不能为空');
					return false
				}
				this.loading = true;
				toLogin({username: this.acount, password: this.password}).then(data => {
					this.loading = false;
					let {code, msg, user } = data;
					if(code != 200){
						this.$message({
							message: msg,
							type: 'error'
						});
					}else{
						this.$utils.session.user = user;
						if(this.$route.redirect){
							this.$router.replace({ path: this.$route.redirect});
						}else{
							this.$router.replace({ path: '/'});
						}
					}
				});
			}
		}
	}
</script>
<style lang="scss" scoped>
	@import '../style/login'
</style>


